(function() {
  'use strict';

  angular
    .module('public')
    .directive('ngSenseItem', ngSenseItem);

  /** @ngInject */
  function ngSenseItem() {
    var directive = {
      restrict: 'A',
      replace: false,
      templateUrl: 'app/components/senseItem/senseItem.html',
      scope: {
          senseId: '@',
          imgId: '@',
          deviceStatus: '='
      },
      link: function(scope, element, attrs) {
        scope.sense = {};

        attrs.$observe('senseId', function(senseId) {
          scope.sense.senseId = senseId;
          scope.getModules();

        });

        attrs.$observe('imgId', function(imgId) {
          scope.sense.imgId = imgId;
        });
      },
      controller: senseItemController,
      controllerAs: 'senseItemController',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function senseItemController(deviceList, deviceStatus, moduleList, $log, $interval, $scope) {
      $scope.delDevice = deviceList.delDevice;
      $scope.logButtonText = 'View';
      $scope.expandButtonText = '+';
      $scope.showModules = false;
      $scope.showLogViewer = false;


      $scope.getModules = function () {
        moduleList.get({senseId: $scope.sense.senseId}, function(response) {
          $scope.modules = angular.fromJson(response.result);
          $scope.moduleCount = $scope.modules.length;
        });
      };


      $interval( function(){
        deviceStatus.get({senseId: $scope.sense.senseId}, function(response) {
          $scope.sense.deviceStatus = angular.fromJson(response.result);
        });
      }, 1000);

      $scope.displayModules = function () {
          if ($scope.expandButtonText == '+') {
            $scope.expandButtonText = '-';
          } else if ($scope.expandButtonText == '-') {
            $scope.expandButtonText = '+';
          }
          $scope.showModules = !$scope.showModules;
      };

      $scope.displayLog = function () {
          if ($scope.logButtonText == 'View') {
            $scope.logButtonText = 'Hide';
          } else {
            $scope.logButtonText = 'View';
          }
          $scope.showLogViewer = !$scope.showLogViewer;
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('public')
    .directive('ngSenseContent', senseContent);

  /** @ngInject */
  function senseContent() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/senseContent/senseContent.html',
      scope: {
          creationDate: '='
      },
      controller: senseContentController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function senseContentController(deviceList, $scope, $modal, $log) {
      var vm = this;

      vm.getDevices = deviceList.getDevices;
      vm.delDevice = deviceList.delDevice;
      // vm.deviceEmpty = (angular.equals(vm.getDevices(),{})) ? true: false;

      // $scope.$watch(function(){ return deviceList.getDevices}, function(newVal, oldVal){
      //   $log.info('data change'+newVal+' '+oldVal);
      // }, true);

      vm.open = function(){

        var modalInstance = $modal.open({
          animation: 1,
          templateUrl: 'addModalContent.html',
          controller: modalController,
          controllerAs: 'vm'
        });

        modalInstance.result.then(function (sense) {

          deviceList.setDevice(sense.id, sense.imgId);

          $log.info('Generating sense id: ' + sense.id + ', imageId:' + sense.imgId);

        }, function () {

          $log.info('Modal dismissed at: ' + new Date());

        });
      };
    }

    function modalController($modalInstance){
      var vm = this;

      vm.imgId = 10 + Math.floor(Math.random() * 54); // random image id

      vm.senseId = ""; // sense id


      vm.ok = function(){
        $modalInstance.close({id: vm.senseId , imgId: vm.imgId});
      };

      vm.cancel = function(){
        $modalInstance.dismiss('cancel');
      };
    }
  }

})();

